.radio {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 61px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: left;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(0, -10px);
    display: block;
    width: 46px;
    height: 46px;
    background-color: $color-input;
    border-radius: 50%;
    cursor: pointer;
  }

  &:hover::before {
    background-color: $color-focus;
  }

  &:active::before {
    background-color: $color-active;
  }
}

.radio__input {
  display: none;
}

.radio__input:checked[type="radio"]:checked ~ .radio__point {
  background-color: $gray;
  border-radius: 50%;

  &:active {
    opacity: 0.3;
  }
}

.radio__point {
  content: "";
  position: absolute;
  left: 15px;
  top: 5px;
  display: block;
  width: 16px;
  height: 16px;
  border: none;
}
